import { BaseValueObject } from '../base';

export class Province extends BaseValueObject<'Province', string> {
  static from(value: string): Province {
    return new Province(value);
  }
  protected validate(value: string): string {
    const trimmed = value.trim();

    if (trimmed.length === 0) {
      throw new Error('Province must not be empty');
    }

    if (trimmed.length > 256) {
      throw new Error('Province has maximum length of 256');
    }

    return value;
  }
}
